* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Quicksand", sans-serif;
  background-color: #282828;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

input {
  width: 100%;
  display: block;
  padding: 1rem;
  border: none;
  color: white;
  background: #282828;
  box-shadow: inset 20px 20px 60px #2b2b2b, inset -20px -20px 60px #3b3b3b;
}
input[type=number] {
  appearance: textfield;
}

main {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
main .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
@media screen and (max-width: 768px) {
  main .container {
    gap: 4rem;
  }
}
main .container form {
  display: grid;
  place-items: center;
  gap: 1rem;
}
@media screen and (max-width: 768px) {
  main .container form {
    width: 100%;
  }
}
main .container form .input-group {
  display: flex;
  gap: 1rem;
}
@media screen and (max-width: 768px) {
  main .container form .input-group {
    width: 100%;
    flex-direction: column;
  }
}
main .container form button {
  padding: 0.8rem 2rem;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  border-radius: 0.5rem;
  color: white;
  background-color: #00b1f7;
}
main .container .timer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
  background: #282828;
  box-shadow: inset 20px 20px 60px #2b2b2b, inset -20px -20px 60px #3b3b3b;
  position: relative;
}
main .container .timer svg {
  width: 80%;
  height: 80%;
  transform: rotate(-90deg);
}
main .container .timer svg circle {
  stroke-dasharray: 160px;
  stroke-dashoffset: 0px;
  stroke-linecap: round;
  stroke-width: 1.5px;
  stroke: #00b1f7;
  fill: none;
  transition: all ease 0.2s;
}
main .container .timer .time {
  width: 70%;
  height: 70%;
  font-size: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
main .container .controls {
  display: flex;
  gap: 3rem;
}